@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@300;400;500;600;700;800;900&family=Righteous&family=Viga&display=swap);
* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
a {
	text-decoration: none;
  	color: inherit;
}
button {
	text-decoration: none;
	font-size: 100%;
	font-family: inherit;
	border: 0;
	padding: 0;
}
ol, ul, li {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
strong {
	font-weight: bold;
}
.root{
    display: flex;
    flex-direction: column;
    align-items: center;
}

body {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;

    display: flex;
    flex-direction: column;
    align-items: center;

    font-family: 'Recursive', sans-serif;
    font-family: 'Righteous', cursive;
    font-family: 'Viga', sans-serif;

    background-color: rgb(48, 48, 48)
}

/*--------------------------Loading--------------------------*/

.loading{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: black;

    display: flex;
    justify-content: center;
    align-items: center;

    -webkit-animation: fadeOut 1s;

            animation: fadeOut 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.loading img{
    width: 30%;
    height: auto;
}

@-webkit-keyframes fadeOut{
    100%{
        opacity: 0;
        visibility: hidden;
    }
}

@keyframes fadeOut{
    100%{
        opacity: 0;
        visibility: hidden;
    }
}

/*--------------------------Header--------------------------*/
.header {
    position: fixed;
    z-index: 1;

    width: 100%;
    height: 100px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgba(255,255,255,.92);
    box-shadow: 0 0 15px #000;

    color: black;
    font-weight: normal;
    font-size: 55px;
    line-height: 40px;

}

main{
    background-color:blue;
    position: relative;
    top: 180px;

    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.title{
    color: white;
    font-weight: normal;
    font-size: 30px;
    line-height: 40px;
    text-align: center;
    margin-top: 160px;
}

.movies{
    width: 80%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 60px;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:space-around;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.movie-card{
    width: 30vh;
    height: 50vh;

    display: flex;
    align-items: center;

    outline: 1px solid rgba(255, 255, 255,.5);    outline-offset: -10px;
    border-radius: 10px;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.295);

    overflow: hidden;
}

.movie-card img{
    width: 100%;
    height: 100%;
    margin: 10px auto 10px auto;

    inset: 0;
    object-fit: cover;
    opacity: 0.80;
    transition: -webkit-transform .75s;
    transition: transform .75s;
    transition: transform .75s, -webkit-transform .75s;

    cursor: pointer;
}

.movies img:hover{
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}

.container-session{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 50px;
    gap: 50px;

    width: 80vw;
    margin-top: 180px;
}

.container-session img{
    outline: 1px solid rgba(255, 255, 255,.5);    outline-offset: -10px;
    border-radius: 20px;
}

.content{
    flex: 1 1 auto;

    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.title-session{
    color: white;
    font-weight: normal;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    margin-bottom: 20px;
}

.date-card{
    background-color: rgba(255, 255, 255, 0.034);
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90%;
    margin: auto;
}

.date{
    width: 450px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

h1{
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 50px;
    text-align: center;
    letter-spacing: 0.1em;
    
    color: white;    
}

.hours{
    display: flex;
    flex-direction: row;
    flex-wrap:nowrap;
    margin: 10px 0 10px 0;
}

.hours p{
    background-color: transparent;
    text-decoration: none;
    border: 3px solid gold;
    border-radius: 10px;

    width: 120px;
    height: 60px;
    margin-right: 20px;

    color: white;
    text-align: center;
    line-height: 60px;
    font-size: 20px;
}

.hours p:hover{
    -webkit-animation: pulsate 1s ease-in-out;
            animation: pulsate 1s ease-in-out;
    background-color: gold;
    color: rgb(49, 49, 49);
}

@-webkit-keyframes pulsate{
    0%{
        box-shadow: 
        0 0 5px #ffe15d,
        0 0 10px #f7d200;
    }
}

@keyframes pulsate{
    0%{
        box-shadow: 
        0 0 5px #ffe15d,
        0 0 10px #f7d200;
    }
}


/*--------------------------Footer--------------------------*/

footer{
    width: 100%;
    height: 100px;
    bottom: 0;
    margin-top: 50px;

    display: flex;
    justify-content: center;
    align-items: center;
}

footer img{
    width: 30px;
}

footer p{
    color: rgb(255, 255, 255);
    font-size: 20px;
    margin: 0 20px 0 10px;
    font-weight: normal;
    line-height: 40px;
}
